@use "base/mixins" as m;
@use "base/variables" as v;

.horas {
  @include m.reset-lista;
  @include m.grid(1, 1rem);
  padding: 2rem 0rem 2rem 0rem;
  @include m.tablet {
    @include m.grid(2, 1rem);
  }

  &__hora {
    color: v.$azul;
    border: 1px solid v.$azul;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;

    &:hover {
      cursor: pointer;
      background-color: lighten(v.$azul, 40%);
      
    }

    &--seleccionada {
      @extend .horas__hora;
      background-color: v.$azul;
      color: v.$blanco;

      &:hover {
        color: v.$azul;
      }
    }

    &--deshabilitada {
      @extend .horas__hora;
      border: 1px solid v.$gris-oscuro;
      color: v.$gris-oscuro;
      opacity: .5;

      &:hover {
        cursor: not-allowed;
        background-color: v.$blanco;
      }
    }
  }
}
