@use "base/mixins" as m;
@use "base/variables" as v;

.formulario {
  display: flex;
  flex-direction: column;

  &__fieldset {
    border: none;
    margin-top: 1rem;
    padding: 0;
  }

  &__legend {
    color: v.$gris;
    font-size: 2.2rem;
    margin-bottom: 2rem;
    @include m.mn-telefono {
      font-size: 2rem;
      margin-bottom: 1rem;
      text-align: center;
    }
  }

  &__campo {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 1.5rem 0;
    &--captcha {
      display: grid;
      place-items: center;
      margin-bottom: 1rem;
    }
  }
  &__contenedor-icono {
    display: flex;
    border-radius: 0.5rem;
    overflow: hidden;
  }
  &__icono {
    background-color: v.$gris-oscuro;
    width: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;

    i {
      color: v.$blanco;
      font-size: 2rem;
    }
  }
  &__label {
    display: block;
    font-size: 1.5rem;
    @include m.telefono {
      font-size: 1.8rem;
    }
    color: rgba(v.$negro, 0.8);
  }
  &__input,
  &__select {
    padding: 1rem;
    border: 1px solid darken(v.$gris-claro, 20);
    border-radius: 1rem;
    @include m.mn-telefono {
      font-size: small;
    }

    &--file {
      border: none;
      @include m.mn-telefono {
        font-size: x-small;
      }
    }
    &--sociales {
      @extend .formulario__input;
      flex: 1;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }

  &__radio {
    display: flex;
    gap: 2rem;
  }

  &__texto {
    margin-top: 3rem;
  }

  &__imagen {
    max-width: 20rem;
  }

  &__listado {
    @include m.tags;
  }

  &__submit {
    @include m.mn-telefono {
      font-size: 1.2rem;
    }
    @include m.boton(v.$azul, v.$blanco);
    border-radius: 0.5rem;
    @include m.tablet {
      width: auto;
      align-self: flex-start;
    }

    &--registrar,
    &--full {
      width: 100%;
    }
  }

  textarea {
    width: 100%; /* Asegura que el textarea ocupe todo el ancho disponible */
    box-sizing: border-box; /* Incluye el padding y borde en el ancho total */
    resize: vertical; /* Permite ajustar la altura verticalmente */
  }
}
